import { useCallback, useEffect, useState } from "react";
import { ApiDocService } from "../../../../../services/taskService";
import { Box } from "../../../../core/MyBox";
import { MyButton } from "../../../../core/MyButton";
import JsonViewerComponent from "../../../test/JsonViewerComponent";
import ModifyApiDocComponent from "./ModifyApiDocComponent";

export default function ApiDocComponent(props: any) {
    const [data, setData] = useState([]);

    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);

    const { setIsLoading } = props;

    const loadData = useCallback(() => {
        if (props.folderKey !== undefined) {
            setIsLoading(true);
            ApiDocService.GetList(props.projectKey, props.folderKey, (data: any) => {
                setData(data.data.data.pagedData);
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.projectKey, props.folderKey, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData])


    function RenderHeaders(data: any) {
        return data.map((data: any, index: number) => {
            return (
                <p key={index}><strong>{data.key}:</strong> {data.value}</p>
            )
        })
    }

    function isObj(str: string) {
        try {
            return JSON.stringify(str) && !!str;
        } catch (e) {
            return false;
        }
    }

    function handleClickDetail(key: string) {
        setKeyEdit(key);
        setVisibleEdit(true);
    }

    function RenderData() {
        return data.map((data: any, index: number) => {
            let dataBody = data.body;

            if (!isObj(dataBody)) {
                dataBody = {};
            }

            let methodStr = "";
            switch (data.method) {
                case 1:
                    methodStr = "Get";
                    break;

                case 2:
                    methodStr = "Post";
                    break;

                case 3:
                    methodStr = "Put";
                    break;

                case 4:
                    methodStr = "Delete";
                    break;
                default:
                    break;
            }
            return (
                <div key={index}>
                    <h3 style={{ color: "blue", fontWeight: "bold" }} onClick={() => {
                        handleClickDetail(data.key);
                    }}><strong>Name: </strong>{data.name}</h3>
                    <p>
                        <strong>{methodStr}</strong>: {data.url}
                    </p>
                    <p>Header: </p>
                    <Box>
                        {RenderHeaders(data.headers)}
                    </Box>
                    <p>Body: </p>
                    <JsonViewerComponent data={dataBody} />
                    <hr />
                </div>
            );
        })
    }

    function handleClickAdd() {
        setVisible(true);
    }

    return (
        <Box>
            <MyButton
                onClick={handleClickAdd}
                value="Thêm API mới" />
            {RenderData()}

            <ModifyApiDocComponent
                projectKey={props.projectKey}
                folderKey={props.folderKey}
                visible={visible}
                setVisible={setVisible}
                loadData={loadData}
                isLoading={props.isLoading}
                setIsLoading={props.setIsLoading} />

            <ModifyApiDocComponent
                projectKey={props.projectKey}
                folderKey={props.folderKey}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                loadData={loadData}
                isLoading={props.isLoading}
                setIsLoading={props.setIsLoading} />
        </Box>
    );
}