import { MyFormItem } from "../../../../core/Input";

export default function HeaderComponent(props: any) {
    function setKeyHeaders(value: string, index: number) {
        var t = props.data;

        if (t[index] === undefined) {
            t[index] = {};
        }

        t[index].key = value;
        props.setData([...props.data]);
    }

    function setValueHeaders(value: string, index: number) {
        var t = props.data;

        if (t[index] === undefined) {
            t[index] = {};
        }

        t[index].value = value;
        props.setData([...props.data]);
    }

    return (
        <div>
            <p>Headers</p>
            <MyFormItem.Input
                label="Key 1"
                value={props.data[0]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 0);
                }} />
            <MyFormItem.TextArea
                label="Value 1"
                value={props.data[0]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 0);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 2"
                value={props.data[1]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 1);
                }} />
            <MyFormItem.TextArea
                label="Value 2"
                value={props.data[1]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 1);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 3"
                value={props.data[2]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 2);
                }} />
            <MyFormItem.TextArea
                label="Value 3"
                value={props.data[2]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 2);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 4"
                value={props.data[3]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 3);
                }} />
            <MyFormItem.TextArea
                label="Value 4"
                value={props.data[3]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 3);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 5"
                value={props.data[4]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 4);
                }} />
            <MyFormItem.TextArea
                label="Value 5"
                value={props.data[4]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 4);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 6"
                value={props.data[5]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 5);
                }} />
            <MyFormItem.TextArea
                label="Value 6"
                value={props.data[5]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 5);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 7"
                value={props.data[6]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 6);
                }} />
            <MyFormItem.TextArea
                label="Value 7"
                value={props.data[6]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 6);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 8"
                value={props.data[7]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 7);
                }} />
            <MyFormItem.TextArea
                label="Value 8"
                value={props.data[7]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 7);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 9"
                value={props.data[8]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 8);
                }} />
            <MyFormItem.TextArea
                label="Value 9"
                value={props.data[8]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 8);
                }} />
            <hr />

            <MyFormItem.Input
                label="Key 10"
                value={props.data[9]?.key}
                setValue={(value: any) => {
                    setKeyHeaders(value, 9);
                }} />
            <MyFormItem.TextArea
                label="Value 10"
                value={props.data[9]?.value}
                setValue={(value: any) => {
                    setValueHeaders(value, 9);
                }} />
            <hr />
        </div>
    )
}