import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../../helper/function";
import { ApiDocService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { MyButton } from "../../../../core/MyButton";
import HeaderComponent from "./HeaderComponent";

export default function ModifyApiDocComponent(props: any) {
    const [headers, setHeaders] = useState([]);
    const [name, setName] = useState('');
    const [method, setMethod] = useState(0);
    const [url, setUrl] = useState('');
    const [body, setBody] = useState<string>('');
    const { setIsLoading } = props;

    const options = [
        {
            value: 1,
            label: "Get"
        },
        {
            value: 2,
            label: "Post"
        }, 
        {
            value: 3,
            label: "Put"
        }, 
        {
            value: 4,
            label: "Delete"
        }
    ]

    function handleClose() {
        props.setVisible(false);
        setHeaders([]);
        setName('');
        setMethod(0);
        setUrl('');
        setBody('');
        if (props.setKeyData !== undefined) {
            props.setKeyData(undefined);
        }
    }

    const [bodyObj, setBodyObj] = useState();

    function isJSON(str: string) {
        try {
            return (JSON.parse(str) && !!str);
        } catch (e) {
            return false;
        }
    }

    const loadData = useCallback(() => {
        if (props.keyData !== undefined) {
            setIsLoading(true);
            ApiDocService.GetDetail(props.projectKey, props.folderKey, props.keyData, (data: any) => {
                console.log(data);
                let row = data.data.data;
                setHeaders(row.headers);
                setName(row.name);
                setMethod(row.method);
                setUrl(row.url);
                setBody(row.body === null ? "" : JSON.stringify(row.body));
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.projectKey, props.folderKey, props.keyData, setIsLoading])

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleOnChangeBody(value: string) {
        if (isJSON(value)) {
            setBodyObj(JSON.parse(value));
        }
        setBody(value);
    }

    function handleSubmitForm() {
        props.setIsLoading(true);
        let bodyReq = {
            name: name,
            headers: headers,
            url: url,
            method: method,
            body: bodyObj
        };

        if (props.keyData === undefined) {
            add(bodyReq);
        } else {
            edit(bodyReq);
        }
    }

    function add(bodyReq: any) {
        ApiDocService.Add(props.projectKey, props.folderKey, bodyReq, (data: any) => {
            Fn.PrintMsgSuccess(data);
            props.loadData();
            handleClose();
        }, () => {
            props.setIsLoading(false);
        })
    }

    function edit(bodyReq: any) {
        ApiDocService.GetDetail(props.projectKey, props.folderKey, props.keyData, (data: any) => {
            bodyReq = Fn.GetTimestamp(data, bodyReq);
            ApiDocService.Edit(props.projectKey, props.folderKey, props.keyData, bodyReq, (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
                handleClose();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function handleDelete() {
        Swal.fire("Xác nhận xóa", "Bạn có muốn xóa dữ liệu không?", "question")
            .then((result) => {
                if (result.isConfirmed) {
                    doDelete();
                }
            });
    }

    function doDelete() {
        props.setIsLoading(true);
        ApiDocService.GetDetail(props.projectKey, props.folderKey, props.keyData, (data: any) => {
            ApiDocService.Delete(props.projectKey, props.folderKey, props.keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
                handleClose();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Drawer
            title={props.keyData === undefined ? "Thêm mới" : "Chỉnh sửa"}
            open={props.visible}
            onClose={handleClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <MyFormItem.Select
                    label="Phương thức"
                    options={options}
                    value={method}
                    setValue={setMethod} />

                <MyFormItem.Input
                    label="URL"
                    value={url}
                    setValue={setUrl} />

                <HeaderComponent
                    data={headers}
                    setData={setHeaders} />

                <MyFormItem.TextArea
                    label="Body"
                    value={body}
                    setValue={(value: string) => {
                        handleOnChangeBody(value);
                    }} />

                <MyButton
                    htmlType="submit"
                    type="success"
                    value={props.keyData === undefined ? "Thêm mới" : "Chỉnh sửa"} />

                <MyButton
                    type="danger"
                    onClick={handleDelete}
                    disabled={props.keyData === undefined}
                    value="Xóa" />
            </Form>
        </Drawer>
    )
}