import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../../helper/function";
import { DocumentService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { MyButton } from "../../../../core/MyButton";
import HeaderComponent from "./HeaderComponent";

export default function ModifyComponent(props: any) {
    const [item, setItem] = useState([]);
    const [name, setName] = useState('');

    const { setIsLoading } = props;

    const loadData = useCallback(() => {
        if (props.keyEdit !== undefined) {
            setIsLoading(true);
            DocumentService.GetDetail(props.projectKey, props.keyEdit, (data: any) => {
                let row = data.data.data;
                setName(row.name);
                setItem(row.headers);
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.projectKey, props.keyEdit, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function onClose() {
        props.setVisible(false);
        if (props.setKeyEdit !== undefined) {
            props.setKeyEdit(undefined);
        }
    }

    function filterArray() {
        var t = item;
        
        t = t.filter((obj: any) => {
            return (obj !== undefined && obj.key !== '');
        })
        return t;
    }

    function handleSubmitForm() {
        let headers = filterArray();
        let body = {
            name: name,
            headers: headers
        }

        props.setIsLoading(true);
        if (props.keyEdit !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        DocumentService.Add(props.projectKey, body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            props.loadData();
        }, () => {
            props.setIsLoading(false);
        })
    }

    function edit(body: any) {
        DocumentService.GetDetail(props.projectKey, props.keyEdit, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            DocumentService.Edit(props.projectKey, props.keyEdit, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function deleteData() {
        props.setIsLoading(true);
        DocumentService.GetDetail(props.projectKey, props.keyEdit, (data: any) => {
            DocumentService.Delete(props.projectKey, props.keyEdit, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
                onClose();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function handleClickDelete() {
        Swal.fire("Xác nhận xóa?", "Bạn có muốn xóa dữ liệu?", "question")
            .then((isConfirmed) => {
                if (isConfirmed.isConfirmed) {
                    deleteData();
                }
            });
    }

    return (
        <Drawer
            title={props.keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"}
            open={props.visible}
            onClose={onClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <HeaderComponent
                    data={item}
                    setData={setItem} />

                <MyButton
                    type="success"
                    isLoading={props.isLoading}
                    htmlType="submit"
                    value={props.keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"} />

                <MyButton
                    type="danger"
                    onClick={handleClickDelete}
                    isLoading={props.isLoading}
                    value="Xóa" />
            </Form>
        </Drawer>
    )
}